}

@media (max-width: 600px) {
  .login__content {
    border: none;
    width: 85vw;
    position: absolute;
    top: 0;
    margin: 0;
  }
}

.login__loading {
  -webkit-animation: a-s 500ms 500ms 1 forwards;
          animation: a-s 500ms 500ms 1 forwards;

  background-color: #f1f1f1;
  height: 4px;
  width: 114%;
  overflow: hidden;
  margin-top: 10px;
  margin: -30px -10px 30px -10px;
  position: relative;
  opacity: 0;
}

.login__loading::before {
  -webkit-animation: a-lb 20s 500ms linear forwards;
          animation: a-lb 20s 500ms linear forwards;

  background-color: #3684eb;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  -webkit-transform: translateX(-300px);
          transform: translateX(-300px);
  width: 100%;
}

@-webkit-keyframes a-s {
  100% {
    opacity: 1;
  }
}

@keyframes a-s {
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes a-lb {
  0% {
    -webkit-transform: translateX(-300px);
            transform: translateX(-300px);
  }
  5% {
    -webkit-transform: translateX(-240px);
            transform: translateX(-240px);
  }
  15% {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
  }
  25% {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
  }
  30% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
  }
  45% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
  }
  50% {
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
  }
  65% {
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
  }
  70% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  95% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  100% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
}

@keyframes a-lb {
  0% {
    -webkit-transform: translateX(-300px);
            transform: translateX(-300px);
  }
  5% {
    -webkit-transform: translateX(-240px);
            transform: translateX(-240px);
  }
  15% {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
  }
  25% {
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
  }
  30% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
  }
  45% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
  }
  50% {
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
  }
  65% {
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
  }
  70% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  95% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  100% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
}

